.header {
  position: relative;
}

.header:after {
  width: 66%;
  height: 100%;
  background-color: #e9eae2;
  z-index: -1;
}

.header svg {
  fill: green;
}

.header__inner {
  display: flex;
}

.header__right-indent {
  display: flex;
  width: 66%;
  flex: 0 66%;
  overflow: hidden;
  position: relative;
}

.header .right {
  width: 25%;
}

.header__img {
  position: relative;
  width: 75%;
}

.header__left-indent {
  width: auto;
  flex: 0 auto;
}
