.header {
    position: relative;
    &:after {
        // @include pos;
        width: 66%;
        height: 100%;
        background-color: #e9eae2;
        z-index: -1;
    }
    svg {
        fill: green;
    }
    &__inner {
        display: flex;
    }
    &__right-indent {
        display: flex;
        width: 66%;
        flex: 0 66%;
        overflow: hidden;
        position: relative;

    }
    .right {
        width: 25%;
    }
    &__img {
        position: relative;
        width: 75%;
    }
    &__left-indent {
        width: auto;
        flex: 0 auto;
    }

}
